body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
#pag{
   margin:5% 0 5% 0;
}
#search{
    margin: 0 20% 0 20%;
    padding: 2% 0 0 0;
}
#pics{
  background-color:rgb(236, 236, 234);
}
#item{  
  list-style-type:none;
  margin: 0 0 2% 1%;
}
#picleft{
    margin: 0 5% 0 5%;
}
#picright{
    margin: 0 5% 0 5%;
}
#text{
  background-color: darkorange;
  width: 100%;
  height: 300px;
  font-family: sans-serif;
  font-style: inherit;
  font-size: x-large;
  margin: 3% 0 0 0;
  text-align: center;
}
#textContext{
  color: aliceblue;
  position: relative;
  top: 35px;
}

body{
  background-color: #1E0731;
}
footer{
  position: absolute;
  margin-top: 300px;
}
footer a{
  color: #F5E53A;
}
footer img{
  width: 32px;
  height: 32px;
  vertical-align: middle;
}

.wrapper{
  position: absolute;
  float: left;
  margin: 140px 100px;
}

.line{
  position: absolute;
  top: 0px;
  width: 2px;
  height: 8px;
  background-color: red;
}

.circle{
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: blue;
}
.circle-top{
  top: 0px;
  left: -3.5px;
}
.circle-bottom{
  bottom: 0px;
  left: -3.5px;
}

.dot{
  position: absolute;
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  left: -1.5px;
}

.line1{
  margin-left: 0px;
  background-color: #2410CB;
  -webkit-animation: line 2s cubic-bezier(0.250, 0, 0.705, 1) infinite;
  animation: line 2s cubic-bezier(0.250, 0, 0.705, 1) infinite;
}
.line1 > span{
  background-color: #D4141E;
}
.line1 > .dotted > .dot{
